import { Component, Prop, State, Event, EventEmitter, h } from '@stencil/core';

export type AvatarAddSize = 'xs' | 'sm' | 'md' | 'lg' | 'xl' | '2xl';

@Component({
  tag: 'bh-avatar-add',
  styleUrl: 'bh-avatar-add.css',
  shadow: true,
})
export class BhAvatarAdd {
  /**
   * Size of the avatar add button
   */
  @Prop() size: AvatarAddSize = 'md';

  /**
   * Whether the button is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Whether to show tooltip on hover
   */
  @Prop() showTooltip: boolean = false;

  /**
   * Tooltip text
   */
  @Prop() tooltipText: string = 'Add user';

  /**
   * Track hover state
   */
  @State() isHovered: boolean = false;

  /**
   * Track focus state
   */
  @State() isFocused: boolean = false;

  /**
   * Emitted when the button is clicked
   */
  @Event() bhClick!: EventEmitter<MouseEvent>;

  private handleClick = (event: MouseEvent): void => {
    if (!this.disabled) {
      this.bhClick.emit(event);
    }
  };

  private handleKeyDown = (event: KeyboardEvent): void => {
    if ((event.key === 'Enter' || event.key === ' ') && !this.disabled) {
      event.preventDefault();
      this.bhClick.emit(event as unknown as MouseEvent);
    }
  };

  render() {
    const buttonClasses = {
      'avatar-add': true,
      [`avatar-add-${this.size}`]: true,
      'avatar-add-hover': this.isHovered && !this.disabled,
      'avatar-add-focus': this.isFocused && !this.disabled,
      'avatar-add-disabled': this.disabled,
    };

    return (
      <div class="avatar-add-container">
        <button
          type="button"
          class={buttonClasses}
          disabled={this.disabled}
          onClick={this.handleClick}
          onKeyDown={this.handleKeyDown}
          onMouseEnter={() => {
            if (!this.disabled) this.isHovered = true;
          }}
          onMouseLeave={() => {
            this.isHovered = false;
          }}
          onFocus={() => {
            if (!this.disabled) this.isFocused = true;
          }}
          onBlur={() => {
            this.isFocused = false;
          }}
          aria-label={this.tooltipText}
          part="button"
        >
          <span class="avatar-add-content">
            <span class="material-symbols-outlined avatar-add-icon">add</span>
          </span>
        </button>

        {this.showTooltip && (
          <bh-tooltip
            text={this.tooltipText}
            visible={this.isHovered && !this.disabled}
            position="bottom"
          />
        )}
      </div>
    );
  }
}
